import axios from 'axios'
import { useState, useEffect } from 'react'
import Web3 from 'web3'
import ERC20ABI from '../tomb-finance/ERC20.abi.json'
import useETHPrice from './useETHPrice'

const web3 = new Web3('https://developer-access-mainnet.base.org')
const WETH = new web3.eth.Contract(ERC20ABI, "0x4200000000000000000000000000000000000006")

const useBASETreasuryBalance = () => {
    const [balance, setBalance] = useState(0)
    const { price } = useETHPrice()
    

    useEffect(() => {
        getBalance()
        const interval = setInterval(() => {
            getBalance()
        }, 10000)
        return () => {
            clearInterval(interval);
        }
    }, [])
    return { balance };
    
    async function getBalance() {
        const bal = await WETH.methods.balanceOf("0x97df582cabb7a525268255d6702c47150f2783d5").call()
        const formattedbal = Number(web3.utils.fromWei(bal)).toFixed(2)
        setBalance(formattedbal)
    }
}

export default useBASETreasuryBalance;